import React, { useState, useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";
import { useForm, Controller } from "react-hook-form";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import { mcheckBoxItems1 } from '../dashboard/data1';

export default function CheckboxesGroup(props) {
  const cItems  = props.chkdItems;
  const [defaultNames, setDefaultNames] = useState([]); 
  console.log("1. props - defaultNames are: ", cItems);
  
  const getUser = async () => {  
     await setDefaultNames(cItems); 
   console.log('2. object defautlNames = ', defaultNames)
  }
  useEffect(() => {
      getUser();
  }, []);
  const { control, handleSubmit } = useForm({
    defaultValues: { names: defaultNames }
  });

  const [checkedValues, setCheckedValues] = useState(defaultNames);

  function handleSelect(checkedName) {
    
    const newNames = checkedValues?.includes(checkedName)
      ? checkedValues?.filter(name => name !== checkedName)
      : [...(checkedValues ?? []), checkedName];
    setCheckedValues(newNames);

    return newNames;
  }
    
  return (
  
    <form onSubmit={handleSubmit(data => console.log(data))}>
      {mcheckBoxItems1.map(name => (
        <FormControlLabel
          control={
            <Controller
              name="names"
              render={({ onChange: onCheckChange }) => {
                return (
                  <Checkbox
                    checked={checkedValues.includes(name)}
                    onChange={() => onCheckChange(handleSelect(name))}
 
                  />
                );
              }}
              control={control}
            />
          }
          key={name}
          label={name + "     |"} 
          
        />
    
      ))}
      <button>Submit</button>
    </form>
    
  );
}
