import React, { useState, useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";
import Button from '@material-ui/core/Button';
import SaveIcon from '@material-ui/icons/Save';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import firebase from "../dashboard/firebase";
import moment from 'moment';
import { Link } from "react-router-dom";
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Select from 'react-select';
import { mcheckBoxItems } from '../dashboard/data';
import { mcheckBoxItems1 } from '../dashboard/data1';

import { useForm, Controller } from "react-hook-form";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Test from '../dashboard/Test';
import { Suspense } from "react";

const useStyles = makeStyles({
  btn: {
        marginRight: '50px',
        marginBottom:'10px',
       },
  
});

const EditUser = (props) => {
  let history = useHistory();
  const { id } = useParams();
  const [user, setUser] = useState([]);
  const [DType, setDocType] = useState([]);
  const [checkedItem, setCheckedItem] = useState([]);
  //const dt = ["Passport", "Visa", "POA", "Affidavit"];
  const {Name, Email, Phone, Category, DateCreated, Notes, DocsNo } = user;
  
  const ref = firebase.firestore().collection("usersCollection").doc(id);
  const classes = useStyles();
  
  const getUser = async () => {  
 
    await ref.onSnapshot((doc) => {
    setUser(doc.data());
    setDocType(doc.data().DocType);
    console.log(`GETUSER defaultNames (DType) are = ` + DType);
    });
          
  }
  useEffect(() => {
      getUser();
     
  }, []);
  
  const onInputChange = e => 
  {
    setUser({ ...user, [e.target.name]: e.target.value });
  };
  
  const onChecked = e => 
  {
    
    setCheckedItem({ ...checkedItem, [e.target.name]: e.target.checked });
    //console.log(`Option selected:`, checkedItem)
  };
  const onSelectionChange = e => 
  {
    //console.log(`Option selected:`, e);
  };

// EDIT FUNCTION
const updateUser = async e => {
  e.preventDefault();
  ref.set(user)
  history.push("/");
};

const { control, handleSubmit } = useForm({
  defaultValues: {
    bill: "bill",
    luo: "",
    Passport: "Passport",
  }
});

const mcheckedDoctTypes = mcheckBoxItems.filter(x => DType.indexOf(x) !== -1);

  return (
    <Paper>
    <div className="container">
      <div className="pb-4">
        <h2 className="text-center mb-4">Edit User</h2>
        <form onSubmit={e => updateUser(e)}>
          <div className="form-group">
          <label>Name</label>
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Your Name"
              name="Name"
              id="Name"
              value={user.Name}
              onChange={e => onInputChange(e)}
            />
            
          </div>
          <div className="form-group">
          <label>Email</label>
            <input
              type="email"
              className="form-control form-control-lg"
              placeholder="Enter Your Email"
              name="Email"
              value={user.Email}
              onChange={e => onInputChange(e)}
            />
          
          </div>
          <div className="form-group">
            
            <label>Phone</label>
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Your Phone"
              name="Phone"
              value={user.Phone}
              onChange={e => onInputChange(e)}
            />
            
          </div>
          <div className="form-group">
          <select name="Category" value={user.Category} onChange={e => onInputChange(e)} className="form-control form-control-sm" aria-label=".form-select-sm example">
                <option selected>{user.Category}</option>
                <option value="Attestation">Attestation</option>
                <option value="Statutory Declaration">Statutory Declaration</option>
                <option value="Confirm Identity">Confirm Identity</option>
                <option  value="Divorce">Divorce</option>
          </select>
          </div>
          <div className="form-group">
          <label>Number of Docs</label>
            <input
              autoComplete="off"
              type="number"
              className="form-control form-control-sm"
              placeholder="Number of documents"
              name="DocsNo"
              value={user.DocsNo}
              onChange={e => onInputChange(e)}
            />
          </div>
          <div className="form-group">
          <label>Document Types</label>
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Your Category"
              name="DocType"
              value={user.DocType}
              onChange={e => onInputChange(e)}
            />
          </div>


          <div className="form-group">
              <div>
                    <label><strong>Document Types</strong></label> 
                  <div class="row ml-1 mr-1 pl-2 pt-2 border rounded">
                      
                        <div class="form-check form-check-inline">
                          <Select 
                            defaultValue={[mcheckBoxItems[0], mcheckBoxItems[3]]} 
                            isMulti 
                            options={mcheckBoxItems}
                            name={mcheckBoxItems.value}
                            className="basic-multi-select mb-2" 
                            classNamePrefix="select" 
                            onChange={e => onSelectionChange(e)}
                          />
                        </div>
                  </div>        
              </div>
            </div> 

            <div className="form-group">
              <div>
                    <label><strong>Document Types</strong></label> 
                    <div class="row ml-1 mr-1 pl-2 pt-2 border rounded">  
                    
                    <Test chkdItems ={DType} />    
                  
                    </div> 
              </div>
            </div> 



            <div className="form-group">
              <div>
                    <label><strong>Document Types</strong></label> 
                  <div class="row ml-1 mr-1 pl-2 pt-2 border rounded">
                      
                        <div class="form-check form-check-inline">
                        {["bill", "luo", "Passport"].map(name => (
        <Controller
          key={name}
          name={name}
          as={
            <FormControlLabel
              control={<Checkbox value={name} />}
              label={name}
            />
          }
          valueName="checked"
          type="checkbox"
          onChange={([e]) => {
            return e.target.checked ? e.target.value : "";
          }}
          control={control}
        />
      ))}
                        </div>
                  </div>        
              </div>
            </div>






          <div className="form-group">
                  <label><strong>Notes</strong></label>
                  <textarea value={user.Notes}  onChange={e => onInputChange(e.checked)} name="Notes" className="form-control form-control-sm" aria-label="With textarea" />
          </div>  

          <div className="form-group">
            <label>Record was created on</label>
            <input
              type="text"
              className="form-control form-control-lg"
              name="Category"
              value={moment(user.DateCreated && user.DateCreated.toDate()).format('llll')}
              readOnly
            />
          </div>
          
          <Button  className={classes.btn} type="submit" variant="contained" color="secondary" 
        startIcon={<SaveIcon />}>
          Update Record
        </Button>
        <Link to={`/`}>
        <Button className={classes.btn} type="submit" variant="contained" color="primary" 
        startIcon={<ArrowBackIcon />}>
          Go Back
        </Button>
                 </Link>
        
        </form>
      </div>
    </div>
    </Paper>
  );
};

export default EditUser;