import React, { useState, useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";
import { useForm, Controller } from "react-hook-form";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import { mcheckBoxItems1 } from '../dashboard/data1';
import firebase from "../dashboard/firebase";
import { ThemeConsumer } from "styled-components";

export default function Checkboxes() {


const ref = firebase.firestore().collection("usersCollection").doc("e3EKrNjCT5VDiDGJodFs");
useEffect(() => 
{
  getUser();
  
}, []);

const [defaultNames1, setDefaultNames] = useState([]);
const [loading, setLoading] = useState(false);

const getUser = async () => {  
 
  await ref.onSnapshot((doc) => {
  setDefaultNames(doc.data().DocType);
  console.log(`GETUSER defaultNames are = ` + doc.data().DocType);
  });
  setLoading(true);
    
}
 
useEffect(() => {
    getUser();
   
}, []);

const defaultNames = defaultNames1; 
  
  
  console.log("checkedItems are: ", defaultNames);
  const [checkedValues, setCheckedValues] = useState(defaultNames);

  const { control, handleSubmit } = useForm({
    defaultValues: { names: defaultNames }
  });

  

  function handleSelect(checkedName) {
    const newNames = checkedValues?.includes(checkedName)
      ? checkedValues?.filter(name => name !== checkedName)
      : [...(checkedValues ?? []), checkedName];
    setCheckedValues(newNames);
    return newNames;
  }
    
  return (
  <>
  {loading ? (defaultNames) : (
        <div className="spinner-border text-primary" role="status">
        <span className="visually-hidden">Loading...</span>
      </div>
  )}
    <form onSubmit={handleSubmit(data => console.log(data))}>
      {mcheckBoxItems1.map(name => (
        <FormControlLabel
          control={
            <Controller
              name="names"
              render={({ onChange: onCheckChange }) => {
                return (
                  <Checkbox
                    checked={checkedValues.includes(name)}
                    onChange={() => onCheckChange(handleSelect(name))}
 
                  />
                );
              }}
              control={control}
            />
          }
          key={name}
          label={name + "     |"} 
          
        />
    
      ))}
      <button>Submit</button>
    
    </form>
    
   </> 
  );
}
