import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
//import Link from '@material-ui/core/Link';
import {withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Hidden from '@material-ui/core/Hidden';
import Paper from '@material-ui/core/Paper';
import Title from './Title';
import VisibilityOutlinedIcon from '@material-ui/icons/VisibilityOutlined';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import HighlightOffRoundedIcon from '@material-ui/icons/HighlightOffRounded';
import firebase from "./firebase";

const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.primary.main,
      color: theme.palette.common.white,
    },
    body: {
      fontSize: 12,
    },
    
  }))(TableCell);
  
  const StyledTableRow = withStyles((theme) => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
      },
    },
  }))(TableRow);

    
// Generate Order Data

function preventDefault(event) {
  event.preventDefault();
}

const useStyles = makeStyles((theme) => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
  btnRightMargin:{
    marginRight: 3,
    height:15,
    fontSize:10,
    

  }
}));
export default function UsersTable(props) {
  const classes = useStyles();
  const [users, setUser] = useState([]);
  const [count, setCount] = useState();
  
  const db = firebase.firestore().collection("usersCollection");

  //REALTIME GET FUNCTION
  function getUsers() 
  {
   db.orderBy("DateCreated", "desc").onSnapshot((querySnapshot) => 
      {
          const items = [];
          querySnapshot.forEach((doc) => 
          {
            const { Name, Email, Phone, Category, DateCreated } = doc.data();
            items.push({ id:doc.id, doc, Name, Email, Phone, Category, DateCreated });
          });
          setUser(items);
          setCount(querySnapshot.size);
      });
  }
 
  useEffect(() => {
    getUsers();
  }, []);

  //DELETE FUNCTION
  function deleteUser(user) {
      db.doc(user.id).delete()
      .catch((err) => {
        console.error(err);
      });
  }

   return (
    
    <Paper className={classes.paper}>
    <React.Fragment>
      <Title>Home Page - {count}</Title>
      <TableContainer component={Paper}>
      <Table size="small" className={classes.Table}>
        <TableHead>
          <TableRow>
            <StyledTableCell>#</StyledTableCell>
            <StyledTableCell>Name</StyledTableCell>
            <StyledTableCell>Email</StyledTableCell>
            <Hidden smDown>
            <StyledTableCell className={classes.Paper}>Phone</StyledTableCell>
            </Hidden>
            <StyledTableCell>Action</StyledTableCell>
          </TableRow>
        </TableHead>

        <TableBody>
          {users.map((user) =>  (
                     
            <StyledTableRow>
              
              <StyledTableCell >{new Date(user.DateCreated.seconds * 1000).toLocaleString("en-NZ")} </StyledTableCell>  
              <StyledTableCell>{user.Name}</StyledTableCell>
              <StyledTableCell>{user.Email}</StyledTableCell>
              <Hidden smDown>
              <StyledTableCell className={classes.Paper}>{user.Phone}</StyledTableCell>
              </Hidden>
              
              <StyledTableCell>
                <Link  to={`/users/`}>
                  
                  <VisibilityOutlinedIcon color="action" fontSize="small" />
                  
                </Link>
                 
                <Link to={`/users/edit/${user.id}`}>
                  <EditTwoToneIcon fontSize="small" />
                 </Link>
                
                 <Link onClick={() => deleteUser(user)}>
                  <HighlightOffRoundedIcon color="secondary" fontSize="small" />
                </Link>
              </StyledTableCell>
             
            </StyledTableRow>
            
              ))}
        </TableBody>
      </Table>
      </TableContainer>
      <div className={classes.seeMore}>
        <Link color="primary" href="#" >
          See more orders
        </Link>
       
      </div>
      
    </React.Fragment>
    </Paper>
    
    
  );
  
}
