import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import firebase from "./firebase";
import Select from 'react-select';
import { mcheckBoxItems } from './data';

const AddUser = () => {
  let history = useHistory();
 
  const [user, setUser] = useState([]);
  const [checkedItems, setCheckedItems] = useState([]);
  const {Name, Email, Phone, DocsNo, DateCreated, Notes, Category } = user;
  
  const ref = firebase.firestore().collection("usersCollection");
  const onInputChange = e => {
    setUser({ ...user, [e.target.name]: e.target.value, "DateCreated": new Date(), "DocType": checkedItems, "DocTypeStatus": "checked" });
    
  };
  
  const handleChange = (e) => {
    let data = checkedItems;
    data.push(e.target.value)
    setCheckedItems(data)
    //console.log(checkedItems)
    
  };
   

  // ADD FUNCTION
 function onSubmit(e) {
 
  ref
    //.doc() use if for some reason you want that firestore generates the id
    .doc()
    .set(user)
    .catch((err) => {
      console.error(err);
    });
    history.push("/");
    
}

  
  let checkBoxItems = ['Passport', 'Birth Certificate', 'POA', 'Visa', 'Death Certificate', 'Affidavit', 'Marriage Certificate',
                       'Photo', 'Other', 'Application Forms'  
                      ];
  return (
     
    
    <Paper>
       
    <div className="container" style={{fontSize:12}}>
        
    <div className="pb-4" >
    <Typography component="h2" variant="h6" color="secondary" align="center"  gutterBottom>
      Add User
    </Typography>
     
        <form autoComplete="off" onSubmit={e => onSubmit(e)}>
          <div className="form-group">
            <input
            required
              autoComplete="off"
              type="text"
              className="form-control form-control-sm"
              placeholder="Enter Client Name"
              name="Name"
              value={Name}
              onChange={e => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input 
              autoComplete="off"
              type="Email"
              className="form-control form-control-sm"
              placeholder="Enter Client Email"
              name="Email"
              value={Email}
              onChange={e => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              autoComplete="off"
              type="number"
              className="form-control form-control-sm"
              placeholder="Enter Client phone"
              name="Phone"
              value={Phone}
              onChange={e => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              autoComplete="off"
              type="number"
              className="form-control form-control-sm"
              placeholder="Number of documents"
              name="DocsNo"
              value={DocsNo}
              onChange={e => onInputChange(e)}
            />
          </div>
          <div className="form-group">
          <select name="Category" value={Category} onChange={e => onInputChange(e)} className="form-control form-control-sm" aria-label=".form-select-sm example">
                <option selected>Select Category</option>
                <option value="Attestation">Attestation</option>
                <option value="Statutory Declaration">Statutory Declaration</option>
                <option value="Confirm Identity">Confirm Identity</option>
                <option  value="Divorce">Divorce</option>
          </select>
          </div>

          <div className="form-group">
          <Select 
                            defaultValue={[mcheckBoxItems[0], mcheckBoxItems[3]]} 
                            isMulti 
                            options={mcheckBoxItems}
                            name="colors" 
                            className="basic-multi-select" 
                            classNamePrefix="select" 
                            onChange={e => onInputChange(e)}
                          />
          </div>


            <div className="form-group">
            
              <div>
                    <label><strong>Document Types</strong></label> 
                  <div class="row ml-1 mr-1 pl-2 pt-2 border rounded">
                      {   checkBoxItems.map((result) =>  (
                        <div class="form-check form-check-inline">
                          <input onChange={(e) => handleChange(e)}  value={result} name="Dtype" id={result} type="checkbox" />
                          <label for="checkbox2">{result}</label>
                        </div>
                    ))}
                  </div>        
              </div>
           
            </div>  
          
          <div className="form-group">
                  <label><strong>Notes</strong></label>
                  <textarea value={Notes}  onChange={e => onInputChange(e)} name="Notes" className="form-control form-control-sm" aria-label="With textarea" />
          </div>  

          <button className="btn btn-primary btn-block" >Add User</button>
        </form>
      </div>
    </div>
    </Paper>
   
  );
};

export default AddUser;